import { MediaMatcher } from '@angular/cdk/layout';
import { ChangeDetectorRef, Component, OnDestroy, Input, ViewChild, ComponentFactoryResolver } from '@angular/core';
import { PxtContentBody } from '../../directives/pxt-content-body';

import { Routes } from '@angular/router';
import { PxtApp } from './pxt-app';
import { PxtAppModel } from './model/pxt-app.model';
import { PxtAppComponentService } from '../../services/pxt-app-components.service';

@Component({
  selector: 'pxt-app',
  templateUrl: './pxt-app.component.html',
  styleUrls: ['./pxt-app.component.scss']
})
export class PxtAppComponent {

  routes: Routes[] = [];
  submenus: any[] = [];

  menuSelected = "";

  _mobileQueryListener: () => void;
  mobileQuery: MediaQueryList;
  shouldRun = true;

  constructor(changeDetectorRef: ChangeDetectorRef, media: MediaMatcher,
    public componentFactoryResolver: ComponentFactoryResolver, public pxtAppComponentService: PxtAppComponentService) {
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
    pxtAppComponentService.submenusItensObservable.subscribe(routes => {
      if (routes != undefined) {
        this.routes = routes;
      } else {
        this.routes = [];
      }

    });

  }
  ngOnDestroy(): void {
    this.mobileQuery.removeListener(this._mobileQueryListener);
    clearInterval(this.interval);
  }

  fillerNav = Array.from({ length: 50 }, (_, i) => `Nav Item ${i + 1}`);

  currentAdIndex = -1;
  @ViewChild(PxtContentBody) adHost: PxtContentBody;
  interval: any;

  ngOnInit() {
    this.subscribeComponent();
  }
  loadComponent(route: any) {
    this.menuSelected = route.data.text;
    //this.currentAdIndex = (this.currentAdIndex + 1) % this.ads.length;
    let adItem = route;
    let componentFactory = this.componentFactoryResolver.resolveComponentFactory(adItem.component);

    let viewContainerRef = this.adHost.viewContainerRef;
    viewContainerRef.clear();
    let componentRef = viewContainerRef.createComponent(componentFactory);
    //(<Teste1Component>componentRef.instance).data = adItem.data;
  }

  subscribeComponent() {
    debugger;
    this.pxtAppComponentService.loadComponentObservable.subscribe(component => {
      debugger;
      let componentFactory = this.componentFactoryResolver.resolveComponentFactory(component);
      let viewContainerRef = this.adHost.viewContainerRef;
      viewContainerRef.clear();
      let componentRef = viewContainerRef.createComponent(componentFactory);
    });
  }


  selectItemMenu(nav) {
    console.log(nav);
    this.loadComponent(nav);
  }

}
